    font-size: 16px;
}
.similerTextParagraph{
    color:#888C9C ;
    margin:15px 25px ;
    font-size: 16px;
}
.previousButton{
    border: 1px solid #32333D !important;
    border-radius: 5px;
    margin: 0px 10px;
    width: 150px;
    height: 50px;
    background-color: #32333D !important;
    color: #888C9C !important;
}
.previousButton :focus{
    border: 1px solid #32333D !important;
 }
.dropdownButton{
    border: 1px solid #32333D !important;
    border-radius: 5px;
    width: 150px;
