    height: 50px;
    margin: 0px 10px;
    background-color: #32333D !important;
    color: #ffffff !important;
}
.NextButton{
    border: 1px solid #FF4B4B !important;
    border-radius: 5px;
    width: 150px;
    height: 50px;
    margin: 0px 10px;
    background-color: #FF4B4B !important;
    color: #ffffff !important;
}

 button:focus {
    outline: 1px #02020200 ;
    outline: none !important;
}
.ant-space {
    gap: 65px !important;
}
.customDropdown .ant-dropdown-menu {
    background-color: #32333D !important;
  }

  .setScroll{
    overflow: auto;
    /* overflow-y: scroll; */
    height: 200px;
    scrollbar-width: thin;
  }
  .setScroll::-webkit-scrollbar {
    width: 0.2rem;
}
  .setScroll::-webkit-scrollbar {
    height: 20px !important;
  }
.setScroll::-webkit-scrollbar-track {
    background-color:#32333D ;
}
.setScroll::-webkit-scrollbar-thumb {
    /* background-color: #888C9C; */
    background-color: #FFFFFF;
    border-radius: 9px;
    height: 20px!important;
}